.loader {
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  background: #b0b0b0;
  border-radius: 50%;
  margin: 15px 50px;
  animation: shadowPulse 2s linear infinite;
  display: block;
  position: relative;
  box-shadow: -24px 0 #fff, 24px 0 #fff;
}

@keyframes shadowPulse {
  33% {
    background: #fff;
    box-shadow: -24px 0 #000, 24px 0 #fff;
  }

  66% {
    background: #000;
    box-shadow: -24px 0 #fff, 24px 0 #fff;
  }

  100% {
    background: #fff;
    box-shadow: -24px 0 #fff, 24px 0 #000;
  }
}

